import React from "react"

const Cart = (props) => {
  const images = require.context('./../images',true)
  const image = images(props.order.item.image).default
  const name = props.order.item.name
  const price = props.order.item.price
  const totalPrice = props.order.totalprice
  
  const HandleDelete = (ev) => {
    props.DeleteFromCart(props.order)
  }

  return(
    <div className="item">
      <div className="img-container">
        <img src={image} alt={name} />
      </div>
      <p className="item-ele name">{name}</p>
      <p className="item-ele price">Cost: {price}</p>
      <p className="item-ele total">Total: {totalPrice}</p>
      <button className="btn closebutton" onClick={HandleDelete}>Remove</button>
    </div>
  )
}

export default Cart