import React from 'react';
import './App.css';
import {BrowserRouter as Router,Route,Switch} from 'react-router-dom'
import Cart from './components/Cart'
import Item from './components/Item'
import Home from './components/Home'
import Navbar from './components/Navbar'
import {items} from './itemData'
function App() {
  const itemData = items
  const [cartData, setcartData] = React.useState([])

  const AppendToCart = (item) => {
    const selectedItem = GetItemFromId(item.itemid)
    const quantity = item.quantity
    const cart = [...cartData]
    const total = Number(selectedItem.price)*Number(quantity)
    cart.push({"item": selectedItem, "quantity": quantity, "totalprice": total})
    console.log(cart)
    setcartData(cart)
  }

  const DeleteFromCart = (order) => {
    const cart = [...cartData]
    for(var i = 0;i<cart.length;i++){
      if(cart[i] === order){
        cart.splice(i,1)
        setcartData(cart)
        return
      }
    }
  }

  const GetItemFromId = (id) => {
    for(var i=0;i<itemData.length;i++){
      if(itemData[i].id===Number(id)){
        return itemData[i]
      }
    }
  }

  const Items = itemData.map((item) => <Item item={item} AppendToCart={AppendToCart} key={item.id}/>)

  const CartItems = cartData.length>0?cartData.map((item) => <Cart order={item} DeleteFromCart={DeleteFromCart} key={item}/>): <div className="cart-empty"><h1>Your cart is empty</h1></div>

  return (
    <Router>
      <div className="App">
        <Navbar />
        <Switch>
          <Route exact path={process.env.PUBLIC_URL + '/'} component={Home}></Route>
          <Route exact path={process.env.PUBLIC_URL + '/shop'} render={() => <div><h1 className="title">Welcome to our shop.</h1>{Items}</div>}></Route>
          <Route exact path={process.env.PUBLIC_URL + '/cart'} render={() => <div><h1 className="title">Your Cart: </h1>{CartItems}</div>}></Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
