import React from "react"

const Item = (props) => {
  const images = require.context("./../images",true)
  const image = images(props.item.image).default
  const name = props.item.name
  const price = props.item.price
  const id = props.item.id
  const [count, setCount] = React.useState(0)
  
  const Increment = () => {
    setCount(count + 1)
  }

  const Decrement = () => {
    if(count<=0)
    return
    setCount(count - 1)
  }

  const HandleChange = (ev) => {
    const value = ev.target.value 
    setCount(value)
  }

  const HandleSubmit = (ev) => {
    if(count===0)
    return
    const itemId = ev.target.getAttribute("itemid")
    const itemObject = {
      "itemid": itemId,
      "quantity": count 
    }
    props.AppendToCart(itemObject)
  }
  return(
    <div className="item">
      <div className="img-container">
        <img src={image} alt={name}/>
      </div>
      <p className="item-name">{name}</p>
      <p className="item-price">Cost: {price}</p>
      <div className="quantity-div">
        <input type="number" name="count" value={count} onChange={HandleChange} /><br></br>
        <button onClick={Increment} className="btns">+</button>
        <button onClick={Decrement} className="btns">-</button>
      </div>
      <button className="btn cartbutton" itemid={id} onClick={HandleSubmit}>Add to Cart</button>
    </div>
  )

}

export default Item